	module.exports = {"data":{"site":{"siteMetadata":{"title":"Glen Cheney"}},"markdownRemark":{"html":"<p>Compdrop is a simple way to view website designs in a desktop browser. Just drag and drop your images into the browser.</p>\n<h2>Improving the workflow</h2>\n<p>For design reviews, designers at Odopod would export their Photoshop files to jpgs (called ”<a href=\"https://en.wikipedia.org/wiki/Comprehensive_layout\">comps</a>”) on our internal server. They then gathered in a meeting room and pulled up the images in Preview.app to critique the work. In 2013, one of the developers built <a href=\"http://cmpdrp.com\">cmpdrp.com</a> as a drag-n-drop preview of the designs that automatically scaled them to actual size, showed the designs within a browser’s chrome, and allowed clicking through them.</p>\n<h2>PWA all the things!</h2>\n<p>Late in 2017 I had some idle time and really wanted to do something with Vue and TypeScript. I decided to rebuild the comp drop app, make it a <abbr title=\"Progressive Web App\">PWA</abbr>, and add some new features to it.</p>\n<p>I started off the project with a <a href=\"https://github.com/vuejs-templates/pwa\">Vue PWA scaffold</a> and added TypeScript to it. The PWA template handled all the service worker caching and offline needs and I started building the new Compdrop.</p>\n<h2>New features</h2>\n<p>After soliciting feedback from the designers and watching how they used the old compdrop, I began to add new features to the app.</p>\n<ul>\n<li>Dropping a directory of images on the page instead of selecting all within a directory.</li>\n<li>Retina image scaling for designs done at 2x size.</li>\n<li>Added a mode which allowed images to be scrolled.</li>\n<li>New keyboard shortcuts.</li>\n<li>Added support for “collections”. Designers used to open multiple tabs of cmpdrp, one for each person to show their work. Now when they drop multiple directories in, Compdrop creates a collection for each one.</li>\n</ul>\n<!-- markdownlint-disable MD033 -->\n<video muted playsinline controls loop poster=\"/compdrop-poster.png\">\n  <source src=\"/static/compdrop-f33d1706394acee134f127fb99856838.webm\" type=\"video/webm; codecs=vp9,vorbis\">\n  <source src=\"/static/compdrop-a28c30c6b3bdf4fa74f0e770a8607fe3.mp4\" type=\"video/mp4\">\n</video>\n<h2>Thoughts on Vue</h2>\n<p>Vue was nice to use, but it feels a little too “automagic” for my liking. Using the <a href=\"https://github.com/vuejs/vue-class-component\"><code class=\"language-text\">vue-class-component</code></a> decorator was also enjoyable because I was accustomed to components in React. Vuex, the state management library for Vue, was very nice to use and easier for me to grok than Redux.</p>","frontmatter":{"title":"Compdrop","imageDescription":"Screenshot of the compdrop app without any files added yet.","href":"https://compdrop.io/","tags":["open source","drag-n-drop","TypeScript","Vue","PWA"]}},"hero":{"sizes":{"base64":"data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAALCAIAAADwazoUAAAACXBIWXMAAAsSAAALEgHS3X78AAAAf0lEQVQoz6WSSQ6AIAwAfbbxO/oYv2DUg0vReJFFvEmtmnhwiSlOQiDQoS0hkFJqL0gMaHJ8EHGTjTH4xD36glLqVf7kQT6S0AG07dD1ogEBws6WkZmu0HbKoSpFTQtG2cseWqguTOMoTbIRzk1Gz44qQMfo+deD8WQasxf0SVb2q4PjlF9wZgAAAABJRU5ErkJggg==","aspectRatio":1.7779433681073025,"src":"/static/compdrop-e7c13835850cf4d227a5c3829018a47c-95436.png","srcSet":"/static/compdrop-e7c13835850cf4d227a5c3829018a47c-4bf5f.png 640w,\n/static/compdrop-e7c13835850cf4d227a5c3829018a47c-7315a.png 1280w,\n/static/compdrop-e7c13835850cf4d227a5c3829018a47c-95436.png 2386w","srcWebp":"/static/compdrop-e7c13835850cf4d227a5c3829018a47c-5d327.webp","srcSetWebp":"/static/compdrop-e7c13835850cf4d227a5c3829018a47c-934d6.webp 640w,\n/static/compdrop-e7c13835850cf4d227a5c3829018a47c-0229d.webp 1280w,\n/static/compdrop-e7c13835850cf4d227a5c3829018a47c-5d327.webp 2386w","sizes":"(max-width: 2386px) 100vw, 2386px"}}},"pathContext":{"slug":"/compdrop/","heroImage":"/images/compdrop.png/","nextProject":{"frontmatter":{"title":"cssnano"},"fields":{"slug":"/cssnano-minifier/"}}}}

